* {
    margin: 0px; 
    padding: 0px; 
    box-sizing: border-box;
    list-style: none;
    border: 0;
    line-height: 1.4em;
    vertical-align: baseline;
    text-decoration: none;
    outline: 0;
}

body, html {
    height: 100%;
    font-size: 16px;
    background-color: peru;
}